body {
    font-family: "Inter", sans-serif;
}

.page-frame {
    width: 100%;
    height: 5000px;
    position: relative;
    overflow: hidden;
    display: none;
}

.page-frame::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 30px;
    background: linear-gradient(180deg, rgba(227,6,19,1) 0%, rgba(227,6,19,0) 100%);
    z-index: 50;
}

.page-frame iframe {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
}